import React, { useState } from 'react';
import Link from 'next/link';
import styles from './Sidebar.module.css';
import { FaBars, FaHome, FaClipboardList, FaHistory, FaCog, FaMoon, FaSun } from 'react-icons/fa';

const Sidebar: React.FC = () => {
  const [isCollapsed, setIsCollapsed] = useState(false);
  const [darkMode, setDarkMode] = useState(false);

  const toggleCollapse = () => setIsCollapsed(!isCollapsed);
  const toggleDarkMode = () => setDarkMode(!darkMode);

  return (
    <div className={`${styles.sidebar} ${darkMode ? styles.dark : ''} ${isCollapsed ? styles.collapsed : ''}`}>
      <button onClick={toggleCollapse} className={styles.toggleButton}>
        <FaBars />
      </button>

      <div className={styles.menu}>
        <h2 className={styles.title}>Menu</h2>
        <ul className={styles.menuList}>
          <li>
            <Link href="/dashboard">
              <a className={styles.link}>
                <FaHome className={styles.icon} />
                {!isCollapsed && 'Dashboard'}
              </a>
            </Link>
          </li>
          <li>
            <Link href="/ambientes">
              <a className={styles.link}>
                <FaClipboardList className={styles.icon} />
                {!isCollapsed && 'Ambientes'}
              </a>
            </Link>
          </li>
          <li>
            <Link href="/agendamento">
              <a className={styles.link}>
                <FaClipboardList className={styles.icon} />
                {!isCollapsed && 'Agendar Reserva'}
              </a>
            </Link>
          </li>
          <li>
            <Link href="/historico">
              <a className={styles.link}>
                <FaHistory className={styles.icon} />
                {!isCollapsed && 'Histórico'}
              </a>
            </Link>
          </li>
        </ul>
      </div>

      <div className={styles.footer}>
        <button onClick={toggleDarkMode} className={styles.themeToggle}>
          {darkMode ? <FaSun /> : <FaMoon />}
          {!isCollapsed && (darkMode ? 'Modo Claro' : 'Modo Escuro')}
        </button>
        {!isCollapsed && (
          <Link href="/configuracoes">
            <a className={styles.link}>
              <FaCog className={styles.icon} />
              Configurações
            </a>
          </Link>
        )}
      </div>
    </div>
  );
};

export default Sidebar;
